<template>
<div id="edit-student">
       <h3>Edit student {{full_name}}</h3>
    <div class="row"> 
        <form @submit.prevent="updateEmployee" class="col s12">
            <div class="row">
                <div class="input-field col s12">
                    <input disabled type="text" v-model="student_id" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="full_name" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="phone_number" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="faculty" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="funding_type" required>
                </div>
            </div>
            <button @click='updateStudent' type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>
        </form>
    </div>
</div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-student',
    data () {
        return {
            student_id: null,
            full_name:null,
            phone_number:null,
            funding_type:null,
            faculty:null

        }
    },

    beforeRouteEnter (to, from, next) {
        db.collection('students').where('student_id',
        '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc =>{
                next(vm => {
                    vm.student_id = doc.data().student_id
                    vm.full_name = doc.data().full_name
                    vm.phone_number = doc.data().phone_number
                    vm.funding_type = doc.data().funding_type
                    vm.faculty = doc.data().faculty


                })
            }
            )
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData (){
            db.collection('students').where
            ('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.student_id = doc.data().student_id
                    this.faculty = doc.data().faculty
                    this.phone_number = doc.data().phone_number
                    this.full_name = doc.data().full_name
                    this.funding_type = doc.data().funding_type
                })
            })
        }, 
        deleteStudent(){
            if(confirm('Are you sure you want to delete this item?')){
                 db.collection('students').where
                ('student_id', '==', this.$route.params.student_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                     doc.ref.delete()
                     this.$router.push('/')

                })
            })
            }

},
updateStudent(){
    db.collection('students').where
            ('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    querySnapshot.forEach(doc =>{
                        doc.ref.update(
                        {student_id: this.student_id,
                        full_name:this.full_name,
                        phone_number:this.phone_number,
                        funding_type:this.funding_type,
                        faculty:this.faculty
                        })
                        .then(()=>{
                            this.$router.push({name:'view-student',
                            params:{student_id:this.student_id}})
                        })
                    })
                })
            })
}
}}
</script>


