<template>
<div id="new-student">
    <h3>New Student</h3>
    <div class="row"> 
        <form @submit.prevent="saveStudent" class="col s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="student_id" required>
                    <label>Student ID</label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="full_name" required>
                    <label>Full Name</label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="phone_number" required>
                    <label>Phone Number</label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="faculty" required>
                    <label>Faculty</label>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="funding_type" required>
                    <label>Funding type</label>
                </div>
            </div>
            <button type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>
        </form>
    </div>
    
</div>
</template>

<script>
import db from './firebaseinit'
import { error } from 'util'
export default {
    name: 'new-student',
    data () {
        return {
            student_id: null,
            full_name:null,
            phone_number:null,
            funding_type:null,
            faculty:null
        }
    },
    methods: {
        saveStudent(){
            db.collection('students').add({
                    student_id: this.student_id,
                    full_name:this.full_name,
                    phone_number:this.phone_number,
                    funding_type:this.funding_type,
                    faculty:this.faculty

            })
            .then(docRef => {
                this.$router.push('/')
            })
            .catch(error => console.log(err))
        }
    }

}
</script>


